import * as React from 'react';
import { ItemKeys, ItemSlot, RenderToolbar, SlotType } from '@ali/4ever-component';
import LineHeightButton, { LineHeightButtonProps } from './components/next/lineHeightButton';

const buttons: Partial<Record<ItemKeys, React.ComponentType<LineHeightButtonProps>>> = {
  'lineheight': LineHeightButton
};

export default function createRenderToolbar(
  locale: Record<string, string>,
  sideBarTrigger?: LineHeightButtonProps['sideBarTrigger'],
): RenderToolbar {


  return (props, controller) => {

    const { buttonName, extraProps } = props;

    const Component = buttons[buttonName] as React.ComponentType<LineHeightButtonProps>;
    if (Component) {
      return (
        <ItemSlot display={SlotType.iconMenu}>
          <Component
            controller={controller}
            locale={locale}
            sideBarTrigger={sideBarTrigger}
            {...extraProps}
          />
        </ItemSlot>
      )
    }

    return null
  };
}
